/* section-products */
.products {
    display: flex;
    justify-content: center;
    text-align: center;

    &__cards{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        margin-top: 90px;
    }

    &__image{
        position: absolute;
        top: -250px;
        left: 0px;
    }

    &__image-two{
        position: absolute;
        top: -250px;
        left: 0px;
    }

    &__image-three{
        position: absolute;
        top: -250px;
        left: 0px;
    }

    &__subtitle{
        font-family: $font-logo;
        color: #FFB8CA;
        font-size: 30px;
        font-weight: 400;  
        margin: 20px;
        margin-top: 40px;
    }

    &__title{
        font-family: $font-logo;
        color: #D41443;
        font-size: 58px;
        font-weight: 400;  
        margin: 0;
    }

    &__card-one {
        position: relative;
        margin-top: 183px;
        justify-content: center;
        text-align: center;
        width: 368px;
        height: 535px;
        background-color:#FFB8CA;
        border-radius: 10px;
    }

    &__card-title {
        font-family: $font-logo;
        font-size: 30px;
        font-weight: 400;
        text-align: center;
        color: white;
        margin-top: 180px;
    }

    &__circle-list{
        display: flex;
        justify-content: center;
        padding: 0;
        gap: 3px;
        margin-top: 36px;
    }

    &__circle{
        width: 6px;
        height: 6px;
        background-color: white;
        border-radius: 50%;
    }

    &__card-body {
        font-family: $font-principal;
        line-height:28.16px;
        font-size: 16px;
        font-weight: 700;
        text-align: center;
        color: white;
        width: 304px;
        height: 134px;
        margin: auto;
        margin-top: 20px;
    }

    &__card-two {
        position: relative;
        justify-content: center;
        text-align: center;
        width: 368px;
        height: 535px;
        margin-top: 183px;
        background-color:$card-two-color;
        border-radius: 10px;
    }    

    &__card-three {
        position: relative;
        justify-content: center;
        text-align: center;
        width: 368px;
        height: 535px;
        margin-top: 183px;
        background-color:$card-three-color;
        border-radius: 10px;
    }

    &__info-addone{
        border-radius: 50%;
        width: 40px;
        height: 40px;
        margin-top: 20px;
        cursor: pointer;
    }

    &__info-addtwo{
        border-radius: 50%;
        width: 40px;
        height: 40px;
        margin-top: 20px;
        cursor: pointer;
    }

    &__info-addthree{
        border-radius: 50%;
        width: 40px;
        height: 40px;
        margin-top: 20px;
        cursor: pointer;
    }
}

