/* section-offices */
.office {
    height: auto;
    display: flex;
    justify-content: center;
    background-color: rgba(255, 245, 246, 1);

    &__list{
        display: flex;
        justify-content: space-around;
    }
    
    &__list-one {
        width: 368px;
        height: 538px;
        padding: 30px;
        border: 1px;
        border-radius: 24px;
        box-shadow: 0px 8px 30px 0px rgba(212, 20, 67, 0.10);
    }

    &__list-two{
        width: 368px;
        height: 538px;
        padding: 30px;
        border: 1px;
        border-radius: 24px;
        box-shadow: 0px 8px 30px 0px rgba(212, 20, 67, 0.10);
        margin: 0 0;
    }
    
    &__list-three{
        width: 368px;
        height: 538px;
        padding: 30px;
        border: 1px;
        border-radius: 24px;
        box-shadow: 0px 8px 30px 0px rgba(212, 20, 67, 0.10);
        margin: 0 0;
    }

    &__title-one {
        border-radius: 6px;
        background: #F0D1A5;
        width: 75.103px;
        height: 28px;
        flex-shrink: 0;
        list-style-type: none;
        display: flex;
        align-items: center;
        color: #FFF;
        justify-content: center;
        margin-bottom: 0;
    }

    &__title-two {
        border-radius: 6px;
        background: rgba(194, 226, 151, 1);
        width: 138px;
        height: 28px;
        flex-shrink: 0;
        list-style-type: none;
        display: flex;
        align-items: center;
        color: #FFF;
        justify-content: center;
        margin-bottom: 0;
    }

    &__subtitle {
        color: #000;
        font-family: DM Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 186%; /* 29.76px */
        letter-spacing: 0.64px;
        margin-top: 20px;
        margin-bottom: 0;
    }

    &__body {
        color: #907E82;
        font-family: DM Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 29.28px */
        letter-spacing: 0.64px;
        margin-bottom: 32px;
    }

    &__line {
        width: 288px;
        height: 0px; 
        border: 0.50px #E1E1E1 solid
    }

    &__hour{
        color: #FFA5BA;
        font-family: DM Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 186%;
        margin-bottom: 20px;
        margin-top: 0;
    }

    &__phone {
        color: #907E82;
        font-family: DM Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0.64px;
        margin-bottom: 0;  
        margin-top: 32px;
        &:hover{
            color:rgba(212, 20, 67, 1);
        }
    }

    &__mail{
        color: #907E82;
        font-family: DM Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0.64px;
        margin-bottom: 40px;  
        margin-top: 8px;
        &:hover{
            color:rgba(212, 20, 67, 1);
        }
    }

    &__btn-list{
        display: flex;
        justify-content: center;
        border-color: none;
    }

    &__btn-locations{
        cursor: pointer;
        border: none;
        font-family: $font-principal;
        width: 176px;
        height: 44px;
        background-color: $white;
        color: $about-us-color__button-read;
        border-radius: 20px;
        font-size: 16px;
        font-weight: 700px;
        line-height: 20.16px;
        margin: 30px;
        padding: 5px;
        box-shadow: 0px 8px 30px 0px rgba(212, 20, 67, 0.10);
        &:hover{
        background-color:$primary-color;
        color:$white;
        }
    }

    &__btn-franchise{
        cursor: pointer;
        border: none;
        width: 176px;
        height: 44px;
        background-color: $white;
        color: $about-us-color__button-read;
        font-family: $font-principal;
        border-radius: 20px;
        font-size: 16px;
        font-weight: 700px;
        line-height: 20.16px;
        margin: 30px;
        padding: 5px;
        box-shadow: 0px 8px 30px 0px rgba(212, 20, 67, 0.10);
        &:hover{
        background-color:$primary-color;
        color:$white;
        }
    }
}

