/* section custumer */
.custumer{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-items: center;

    &__img{
        display: flex;
        justify-content: center;
        margin-top: 120px;
        margin-bottom: 16px;
    }

    &__img-woman{
        border-radius: 50%;
    }

    &__comilla{
        display: flex;
        flex-direction: column;
        width: 968px;
        height: 138px;
        background-color: #FAFAFA;
        display: flex;
        position: relative;
        align-items: center;
        justify-content: center;
    }

    &__svg-comillas{
        top: 25px;
        width: 54px;
        height: 40px;
        position: absolute;
        left: 73px;
    }  

    &__parrafo{
        color: #000;
        text-align: center;
        font-family: DM Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 186%; /* 29.76px */
        letter-spacing: 0.64px;
        margin: 0;
        width: 821px;
        border-bottom: 1px solid #E1E1E1;
        padding-bottom: 8px;
    }

    &__list{
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__circle{
        display: flex;
        margin-top: 16px;
        width: 32px;
        height: 4px;
        justify-content: space-around;
    }

    &__circle {
        & div{
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color:#C7133E;
        }
    }
    &__name{
        margin: 16px 0 32px 0px;
        & p {
            color: #000;
            text-align: center;
            font-family: DM Sans;
            font-size: 20px;
            font-style: normal;
            font-weight: 500;
            line-height: 186%; /* 37.2px */
            margin: 0;
            }
        }

    &__svg{
        display: flex;
        gap: 18px;
        width: 75px;
        & div {
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background-color: #D9D9D9;
            margin-bottom: 120px;
            }
        }

    &__coment-one,
    &__coment-two,
    &__coment-three,
    &__name-laura,
    &__name-lara,
    &__name-emily{
        opacity: 0;
        visibility: hidden;
        position: absolute;
    }

    &__svg-three,
    &__svg-two,
    &__svg-one{
        width: 12px;
        height: 12px; 
        position: absolute;  
        background-color: #FFF;
        background-size: contain;
        opacity: 0;
    }

}

